body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;
}
.header {
  padding: 10px;
  background-color: firebrick;
  color: #191a1a;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
}

.header h1 {
  font-size: 25px;
  padding: 0;
  margin: 0;
  text-align: center;
}

.map {
  position: relative;
  width: 100vw;
  height: 100vh;
}

.location-icon {
  font-size: 2rem;
  color: red;
}

.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loader img {
  width: 200px;
}

.loader h2 {
  margin-top: -10px;
}

.location-info {
  position: absolute;
  top: 80px;
  right: 30px;
  width: 300px;
  min-height: 100px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 10px;
  font-size: 12px;
}

.location-info ul {
  list-style-type: none;
  padding: 0;
}

.location-info li {
  padding: 5px 0;
}

.ThemeButton {
  position: absolute;
  top: 60px;
  left: 10px;
}
.EventSelector {
  position: absolute;
  top: 120px;
  left: 10px;
}
.Selector {
  border: 2px solid red;
  border-radius: 5px;
  margin-bottom: 20px;
}
.DeSelector {
  padding: 2px;
  margin-bottom: 20px;
}